.etic-cont{
  font-family: 'Arial Bold', 'Arial';
  font-size: 1em;
  font-weight:bold;
  color: #323232;
  background-color: #CCCCCC;
  min-width: 90px;
  border-radius: 10px;
  min-height: 25px;
  padding-left: 10px;
  text-decoration: none;
  text-align: left;
  margin-left: 10px;
  margin-right: 10px;
  display: table!important;
  vertical-align: middle;
  line-height:1.6em;
    .icon-rem{
      padding-right: 10px;
      font-size: 0.625em;
      outline: 0!important;
      color: #258900;
      padding-left: .625em;
      padding-top: calc(1-0.75em);
      display: table-cell!important;
      vertical-align: middle;
      line-height:1.852em;
      float:left;
      cursor:pointer;
    }
  }

  button:active span, button:focus span ,button:active i, button:focus i ,button:active div, button:focus div{
    position:relative;
    top: 0;
    left: 0;
  }
  .cont-text{
    display: table-cell !important;
    vertical-align: middle;
  }